.articles {
  max-width: 1300px;
  margin: 0 auto 80px;
  padding: 0 10px 0;
  display: flex;
  flex-direction: column;
  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
  }

  &__title {
    margin: 0;
    font-family: 'Prata', Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 100%;
    color: #303030;
  }

  &__read-more-box {
    display: flex;
    transition: opacity .3s ease-out;
  }

  &__read-more {
    margin: 0;
    display: flex;
    align-items: flex-end;
    font-family: 'Inter', Arial, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 100%;
    color: #303030;
    text-transform: uppercase;
  }

  &__read-more-box:hover {
    cursor: pointer;
    opacity: 0.5;
  }

  &__arrow {
    margin-left: 12px;
    background-position: bottom;
    background-repeat: no-repeat;
    background-image: url("../../images/articlesArrow.svg");
    width: 27px;
  }

  &__grid {
    max-width: 1300px;
    margin: 0 auto;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(4, 1fr);

    @media screen and (max-width: 800px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 550px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

}
